*,
*::before,
*::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  user-select: none;
}

body {
  background-color: #fff;
  font-family: "Circe";
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

input {
  padding: 0;
  margin: 0;
  border: 0;
  border: none;
}

button {
  padding: 0;
  border: none;
  cursor: pointer;
}

.containers {
  max-width: 1440px;
  padding: 0 120px;
  margin: 0 auto;
  width: 100%;
}

ol,
ul {
  padding-left: 0;
}

dl,
ol,
ul {
  margin-top: 0;
  margin-bottom: 0;
}

p {
  margin-top: 0;
  margin-bottom: 0;
}

.h1,
.h2,
.h3,
.h4,
.h5,
.h6,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  margin-bottom: 0;
}

.display-n {
  display: none !important;
}

.header {
  width: 100%;
  padding: 30px 0;
  background: transparent;
  position: relative;
  z-index: 10;
}

.header__block {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__logo {
  cursor: pointer;
  height: 50px;
}

.header__l {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 750px;
}

.header__i a {
  font-family: TTNorms;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 19px;
  color: #181818;
  transition: all 0.5s ease;
}

.header__i:hover a {
  color: #000000;
  font-weight: 500;
  transition: all 0.5s ease;
}

.footer {
  width: 100%;
  padding: 40px 0;
}

.footer__wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  width: 100%;
}

.footer__nav {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.footer__nav-logo {
  cursor: pointer;
}

.footer__nav-l {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 660px;
}

.footer__info {
  margin: 60px 0 0 0;
  display: flex;
  width: 100%;
  align-items: flex-start;
  justify-content: space-between;
  padding: 0 140px 0 0;
}

.footer__info-c {
  width: 460px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}

.footer__info-c p {
  font-family: TTNorms;
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  text-transform: uppercase;
  color: rgba(62, 62, 62, 0.6);
}

.footer__info-c a {
  margin: 33px 0 0 0;
  font-family: Roboto;
  font-style: normal;
  font-weight: 700;
  font-size: 34px;
  line-height: 46px;
  color: #313131;
}

.footer__info-c h5 {
  margin: 20px 0 0 0;
  font-family: TTNorms;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 26px;
  color: #111111;
}

.footer__info-a {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  width: 280px;
}

.footer__info-a p {
  font-family: TTNorms;
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  text-transform: uppercase;
  color: rgba(62, 62, 62, 0.6);
}

.footer__info-a address {
  font-family: TTNorms;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 26px;
  color: #111111;
  margin: 32px 0 0 0;
}

.footer__add {
  margin: 70px 0 0 0;
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;
}

.footer__add-i {
  font-family: TTNorms;
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 22px;
  color: #9dafbd;
}

.footer__add-c {
  cursor: pointer;
  font-family: TTNorms;
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 22px;
  color: #9dafbd;
}

.ind-main {
  width: 100%;
  position: relative;
  padding: 40px 0;
}

.ind-main__bg {
  content: "";
  position: absolute;
  top: -110px;
  left: 50%;
  transform: translate(-50%, 0%);
  width: 100%;
  height: calc(100% + 110px);
}

.ind-main__bg img {
  width: 100%;
  height: 100%;
}

.ind-main .containers {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  width: 100%;
}

.ind-main__info {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  width: 480px;
  position: relative;
  z-index: 5;
  margin: 30px 0 0 0;
}

.ind-main__info-t p {
  font-family: TTNorms;
  font-style: normal;
  font-weight: 700;
  font-size: 40px;
  line-height: 54px;
  text-transform: uppercase;
  color: #363636;
}

.ind-main__info-t p span {
  color: #3ea3f2;
}

.ind-main__info-d {
  margin: 45px 0 0 0;
  width: 85%;
}

.ind-main__info-d p {
  font-family: TTNorms;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 30px;
  color: #484848;
}

.ind-main__info-btns {
  margin: 60px 0 0 0;
  position: relative;
  width: 100%;
}

.ind-main__info-p {
  background: #3ea3f2;
  border-radius: 50px;
  padding: 19px 35px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  width: max-content;
}

.ind-main__info-p p {
  font-family: Circe;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 27px;
  color: #ffffff;
}

.ind-main__info-p img {
  margin: 0 0 0 20px;
}

.ind-main__info-i {
  border: 3px solid #3ea3f2;
  background: #ffffff;
  border-radius: 50px;
  padding: 19px 35px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  width: max-content;
  position: absolute;
  top: 50%;
  right: -235px;
  transform: translate(0%, -50%);
}

.ind-main__info-i p {
  font-family: Circe;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 27px;
  color: #0a0a0a;
}

.ind-main__info-i img {
  margin: 0 0 0 20px;
}

.ind-main__video {
  width: 755px;
  height: 500px;
  border-radius: 0 0 0 200px;
  position: relative;
  margin: 0 -120px 0 0;
  overflow: hidden;
  z-index: 5;
}

.ind-main__video::after {
  content: "";
  position: absolute;
  top: 0;
  right: -350px;
  width: 50%;
  height: 100%;
  width: 1200px;
  height: 673px;
  background: linear-gradient(270deg, #3ea3f2 23.33%, rgba(196, 196, 196, 0) 100%);
  z-index: 6;
}

.ind-main__video-video {
  width: 100%;
  height: 100%;
}

.ind-main__decor {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, 0%);
  z-index: 1;
  width: 100%;
  max-width: 1440px;
  height: 100%;
}

.ind-main__decor-i {
  position: absolute;
  width: max-content;
  height: max-content;
  z-index: 1;
}

.ind-main__decor-i:nth-child(1) {
  top: 350px;
  left: 68px;
}

.ind-main__decor-i:nth-child(2) {
  top: 41px;
